import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
@Component({
  providers: [Beyond],
  selector: 'app-list-invoice',
  templateUrl: './list-invoice.component.html',
  styleUrls: ['./list-invoice.component.scss']
})
export class ListInvoiceComponent implements OnInit {
  loginData: any;
  invoiceListDetails: any;
  invoiceListResponse: boolean;
  loginCode: any;
  // searchInvoice: FormGroup;

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.loginCode = this.loginData.loginCode
    this.getListOfInvoices()
  }


  getListOfInvoices() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfCustomerInvoices' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        console.log("invoice List Details" + JSON.stringify(response))
        if (response.status == 200) {
          this.invoiceListDetails = response.response;
          this.invoiceListResponse = true;
        }
        else {
          this.invoiceListResponse = false;
        }
        error => {
        }
      })
  }


  // addInvoice() {
  //   this.router.navigate(['/customer/addInvoice'])
  // }

  viewInvoice(invoiceNo) {
    this.router.navigate(['/customer/viewInvoice', invoiceNo])
  }

  editInvoice() {
    this.router.navigate(['/customer/editInvoice'])
  }
  home(){
    this.router.navigate(['/customer/customerDashboard', this.loginData.loginCode])
  }
}
