import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { multiplewizard } from '../../../utilities/multiplewizard';
import { Imageselector } from 'src/utilities/imageselector';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Global } from '../../../global/global';
import { CustomValidators } from '../../../utilities/customValidators';
import { RegistrationValidator } from '../../../utilities/validator';
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  providers: [multiplewizard, Imageselector, CustomValidators],
  selector: 'app-lead-otp-confirmation',
  templateUrl: './lead-otp-confirmation.component.html',
  styleUrls: ['./lead-otp-confirmation.component.css']
})
export class LeadOtpConfirmationComponent implements OnInit {
  otpGenerationFormGroup: FormGroup;
  passwordFormGroup: FormGroup;
  otp: any;
  customerId: any;
  registrationFormGroup: FormGroup;
  customerData: any;
  categoryName: any;
  subCategoryName: any;
  matchingProData: any;
  zipCode: any;
  catSubCatName: any;
  matchingResponse: any;
  customerStatus: boolean = false;
  matchingResponseStatus: boolean = false;
  matchingStatus: boolean = false;
  submitted = false;
  invalidOTP = false;
  role: any = "CUST"
  constructor(
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
    private spinner: NgxSpinnerService,
  ) {
    this.passwordFormGroup = this.formBuilder.group({
      mobileCode: ['', Validators.required],
      password: ['', Validators.required],
      repeatPassword: ['', Validators.required]
    }, {
      validator: RegistrationValidator.validate.bind(this)
    });
    this.otpGenerationFormGroup = this.formBuilder.group({
      mobileCode: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.multiplewizard.Multiplewizard();
    this.imageselector.imageselector();
    this.catSubCatName = JSON.parse(localStorage.getItem('categories'))
    this.categoryName = this.catSubCatName.category;
    this.subCategoryName = this.catSubCatName.subCategory;
    this.listLeadCustomer(this.activatedRoute.snapshot.params['anyCode'])
    this.getLeadOTP(this.activatedRoute.snapshot.params['anyCode'])
    this.customerId = this.activatedRoute.snapshot.params['anyCode'];
    this.customerData = JSON.parse(localStorage.getItem('response'))
  }
  //LIST LEAD CUSTOMER
  listLeadCustomer(customerId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadDetailsOTPConfirmation/' + customerId, options)
      .subscribe(data => {
        var leadCustomerData = data.json();
        if (leadCustomerData.status == 200) {
          this.customerStatus = true;
          this.customerData = leadCustomerData.response[0];
          this.zipCode = this.customerData.zipCode
        } else {
          this.customerStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //LIST LEAD CUSTOMER
  //GET CONFIRMATION CODE
  getLeadOTP(customerCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadOTP/' + customerCode, options)
      .subscribe(data => {
        var otpData = data.json()
        this.otp = otpData.response[0].otp
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //GET CONFIRMATION CODE
  //VALIDATE CONFIRMATION CODE
  confirm(mobileCodeValue) {
    if (mobileCodeValue == "" || mobileCodeValue == null || mobileCodeValue == undefined){ this.invalidOTP = false; return;}
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/leadOTPValidation/' + this.activatedRoute.snapshot.params['anyCode'] + '/' + mobileCodeValue, options)
      .subscribe(data => {
        var otpValidator = data.json()
        if (otpValidator.status == 200) {
          this.invalidOTP = false
        } else {
          this.invalidOTP = true
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //VALIDATE CONFIRMATION CODE
  //SUBMIT
  signUpBtn() {
    this.submitted = true;
    this.spinner.show()
    if (this.passwordFormGroup.valid == false ||this.invalidOTP == true) {
      this.spinner.hide()
      return false;
    }
    let otpObj = {
      otp: this.customerData.OTP,
      loginCode: this.customerData.customerId,
      mobileNumber: this.customerData.phone,
      password: this.passwordFormGroup.value.repeatPassword,
      emailId: this.customerData.emailId,
      name: this.customerData.firstName,
      role: this.role
    }
    console.log("This is otp Obj: " + JSON.stringify(otpObj))
    this.http.post(Global.url + '/customerPasswordConfm', otpObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.fetchMatchingPros()
          return false;
        }
      })
  }
  //SUBMIT
  //CHECK OUT AS GUEST
  checkOutAsGuest() {
    this.router.navigate(['/customer/leadPreview', this.activatedRoute.snapshot.params['anyCode']])
  }
  //CHECK OUT AS GUEST
  //GET MATCHING PROS
  fetchMatchingPros() {
    let fetchMatchingProObj = {
      "zipCode": this.zipCode,
      "categoryName": this.categoryName,
      "subCategoryName": this.subCategoryName,
      "catCode": this.catSubCatName.catCode,
      "subCatCode": this.catSubCatName.subCatCode,
      "customerId": this.customerId,
      "type": "SINGLE_LEAD_GENERATION"
    }
    console.log("fetchMatchingProObj" + JSON.stringify(fetchMatchingProObj))
    this.http.post(Global.url + '/fetchMatchingPro', fetchMatchingProObj)
      .subscribe(data => {
        var matchingProsData = data.json();
        console.log("matching pro data" + JSON.stringify(matchingProsData))
        if (matchingProsData.status == 200) {
          this.matchingStatus = true;
          this.matchingProData = matchingProsData.response;
          this.addMatchingPro(this.matchingProData)
          this.isMatched({
            isMatched: 'MATCHED',
            customerId: this.customerId
          })
          // console.log("matchingProData"+JSON.stringify(this.matchingProData))
          //alert("Mails are sending for the Matching Pros")
          this.spinner.hide()
          this.router.navigate(['/customer/leadConfirmation', this.activatedRoute.snapshot.params['anyCode']])
        } else if (matchingProsData.status == 404 || matchingProsData.status == 400) {
          this.matchingStatus = false;
          // alert("Someone will reach you shortly from Top Proz")
          this.spinner.hide()
          this.router.navigate(['/customer/leadConfirmation', this.activatedRoute.snapshot.params['anyCode']])
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  //GET MATCHING PROS
  //IS MATCHED
  isMatched(isMatched) {
    console.log(isMatched)
    return new Promise((resolve, reject) => {
      this.http.post(Global.url + '/updateMatchedProStatus', isMatched)
        .subscribe(data => {
          console.log(data.json());
          resolve({
            status: 'success'
          })
        })
    })
  }
  //IS MATCHED
  //MATCHING PRO
  addMatchingPro(matchingProData) {
    return new Promise((resolve, reject) => {
      this.http.post(Global.url + '/addBatchMatchingPro/', matchingProData)
        .subscribe(data => {
          var matchingData = data.json();
          this.matchingResponse = matchingData.response;
          if (matchingData.status == 200) {
            this.matchingResponseStatus = false;
            resolve({
              status: 'success'
            })
            this.toastr.success("Matching Data Added Successfully");
          } else {
            resolve({
              status: 'fail'
            })
            this.toastr.warning("Record adding Failed")
            this.matchingResponseStatus = true;
          }
        }, error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })
    })
  }
  //MATCHING PRO

}
