import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { delay } from 'q';
import { multiplewizard } from '../../../utilities/multiplewizard';
import { Imageselector } from 'src/utilities/imageselector';

import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Global } from '../../../global/global';

@Component({
  providers: [Beyond, multiplewizard, Imageselector],
  selector: 'app-customer-view-my-jobs',
  templateUrl: './customer-view-my-jobs.component.html',
  styleUrls: ['./customer-view-my-jobs.component.scss']
})
export class CustomerViewMyJobsComponent implements OnInit {
  customerId: any;
  leaddCustomerData: any;
  customerData: any;
  serviceName: any;
  customerStatus: boolean = false;
  customerHistoryStatus: boolean = false;
  matchingStatus: boolean = false;
  getQuote: boolean = false;
  getProz: boolean = false;
  customerHistoryData: any;
  emergency: any;
  catSubCatName: any;
  categoryName: any;
  subCategoryName: any;
  editField: any;
  matchingProData: any;
  zipCode: any;
  leadStatus: any;
  leadUpdate: any;
  theEmergencyRequest: boolean = false;
  image: any;
  imgAv: string;
  customerData2: string;
  CurrentUserDetails: any;
  customerID: string;
  loginData: any;
  logData: any;
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.loginData = JSON.parse(localStorage.getItem("login"));
    this.beyond.beyondAdmin()
    this.multiplewizard.Multiplewizard();
    this.imageselector.imageselector();
    this.catSubCatName = JSON.parse(localStorage.getItem('categoryName'))
    console.log(this.activatedRoute.snapshot.params['customerId'])
    this.listLeadCustomer(this.activatedRoute.snapshot.params['customerId'], this.activatedRoute.snapshot.params['leadId'])
    this.listLeadCustomerHistory(this.activatedRoute.snapshot.params['customerId'], this.activatedRoute.snapshot.params['leadId'])
    this.emergency = JSON.parse(localStorage.getItem('emergency'))


  }


  listLeadCustomerHistory(customerId, leadId) {
    console.log("hi customer" + customerId)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewLeadGenCustomerLeadHistoryData/' + customerId + '/' + leadId, options)
      .subscribe(data => {
        var leadCustomerHistoryData = data.json();
        if (leadCustomerHistoryData.status == 200) {
          this.customerHistoryStatus = true;
          this.customerHistoryData = leadCustomerHistoryData.response;
          console.log("questionss.." + JSON.stringify(this.customerHistoryData))
          console.log('customerHistoryData', JSON.stringify(this.customerHistoryData))
          for (var i = 0; i < this.customerHistoryData.length; i++) {
            if (this.customerHistoryData[i].serviceQuestion == "Is this an emergency" && this.customerHistoryData[i].serviceAnswer == "Yes") {
              console.log("in color if" + this.customerHistoryData[i].serviceQuestion)
              this.customerHistoryData[i].color = 'red';
              this.customerHistoryData[i].colorRed = true;
              this.theEmergencyRequest = true;

            } else if (this.customerHistoryData[i].serviceQuestion == "Is this an emergency" && this.customerHistoryData[i].serviceAnswer == "No") {
              console.log("in color if else" + this.customerHistoryData[i].serviceQuestion)
              this.customerHistoryData[i].color = 'green';
              this.customerHistoryData[i].colorRed = false;
              this.theEmergencyRequest = false;
            } else if (this.customerHistoryData[i].serviceQuestion == "" && this.customerHistoryData[i].serviceAnswer == "") {
              console.log("in color if else" + this.customerHistoryData[i].serviceQuestion)
              this.customerHistoryData[i].color = 'green';
              this.customerHistoryData[i].colorRed = false;
              this.theEmergencyRequest = false;
            }
          }
          console.log("customer data" + JSON.stringify(this.customerData))
        } else {
          this.customerHistoryStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  listLeadCustomer(customerId, leadId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewLeadGenCustomerLeadDatas/' + customerId + '/' + leadId, options)
      .subscribe(data => {
        var leadCustomerData = data.json();
        if (leadCustomerData.status == 200) {
          this.customerStatus = true;
          this.customerData = leadCustomerData.response[0];
          this.image = this.customerData.image
          console.log('customerData', JSON.stringify(this.customerData))


          if (this.customerData.image == '' || this.customerData.image == null) {

            this.customerData2 = "../../assets/img/no-image.png"
          } else {
            this.customerData2 = Global.url + this.customerData.image

          }


          this.leadStatus = this.customerData.leadStatus
          this.zipCode = this.customerData.zipCode
          console.log("in lead cust" + JSON.stringify(this.customerData.image))
        } else {
          this.customerStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  okButton() {
    this.router.navigate(['/customer/customerMyJobs'])
  }
  newWorkOrderbtn() {
    alert('Coming Soon')
  }
  home()
  {
    this.router.navigate(['/customer/customerDashboard', this.logData.loginCode])
  }
  list(){
    this.router.navigate(['/customer/customerMyJobs'])
  }
}
