import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../utilities/beyond';
import { delay } from 'q';
import { multiplewizard } from '../../../utilities/multiplewizard';
import { Imageselector } from 'src/utilities/imageselector';

import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Global } from '../../../global/global';
import { CustomValidators } from '../../../utilities/customValidators';

@Component({
  providers: [Beyond, multiplewizard, Imageselector, CustomValidators],
  selector: 'app-list-leads-ta',
  templateUrl: './list-leads-ta.component.html',
  styleUrls: ['./list-leads-ta.component.css']
})
export class ListLeadsTaComponent implements OnInit {
  leadsData: any;
  listOfLeadsData: any;
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.listOfLeads();
  }

  addServiceFee(){
    this.router.navigate(['/listServiceFeesHouseholds1'])
  }

  listOfLeads() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadDetailsList', options)
      .subscribe(data => {
        this.leadsData = data.json();
        this.listOfLeadsData = this.leadsData.response;
        //console.log("hi..."+JSON.stringify(this.listOfLeadsData))
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  click2Call(data){
    this.http.get(Global.url + '/callTwoWay/'+data.phone+'/'+data.proPhone)
    .subscribe(data => {
        var response = data.json()
        if(response.status === 200){
          this.toastr.success('Initiating Call Please Wait...')
        }else{
          this.toastr.success('Unable to initiate call')
        }
    }, error => {
      this.toastr.warning('Unable to Connect to Server')
    })
  }
home(){
  this.router.navigate(['/admin/dashboard'])
}
}
