 //ZIPCODE ENTRY---------
 zipcode() {
   //console.log("zipcode" + this.zipCodeDetails.value.zipCode)
   localStorage.setItem('zipcode', this.zipCodeDetails.value.zipCode)
   //CUSTOMER ID GENERATION
   var val = Math.floor(1000 + Math.random() * 9000);
   this.randamvalue = "CUST" + "" + val
   //console.log("random value" + this.randamvalue)
   localStorage.setItem('customerCode', this.randamvalue)
   //CUSTOMER ID GENERATION    
   // ZIPCODE VALIDATION
   this.questionsFromCatgeory()
   //this.validationZipCodesAPI(this.zipCodeDetails.value.zipCode, "5")
   // ZIPCODE VALIDATION
 }
 //ZIPCODE ENTRY---------
 // VALIDATION ZIPCODE API
 validationZipCodesAPI(zipcode, distance) {
   var headers = new Headers();
   headers.append("Accept", 'application/json');
   headers.append('Content-Type', 'application/json');
   let options = new RequestOptions({ headers: headers });
   this.http.get(Global.url + '/getZipCodes/' + zipcode + "/" + distance, options)
     .subscribe(data => {
       this.zipcodeResponse = data.json();
       //console.log("validationZipCodesAPI" + JSON.stringify(this.zipcodeResponse))
       if (this.zipcodeResponse.status == 200) {
         this.questionsFromCatgeory()
       } else {
         this.toastr.warning('No ZipCode Available...')
       }
     }, error => {
       this.toastr.warning('Unable to Connect to Server')
     })
 }
 // VALIDATION ZIPCODE API
 // QUESTION FROM ZIPCODE
 questionsFromCatgeory() {
   //console.log("in cat questions" + this.activatedRoute.snapshot.params['subCat'])
   //console.log("in cat questions" + this.activatedRoute.snapshot.params['cat'])
   var headers = new Headers();
   headers.append("Accept", 'application/json');
   headers.append('Content-Type', 'application/json');
   let options = new RequestOptions({ headers: headers });
   this.http.get(Global.url + '/catQuestionsOptions/' + this.activatedRoute.snapshot.params['cat'] + "/" + this.activatedRoute.snapshot.params['subCat'], options)
     .subscribe(data => {
       this.catquestionsOptions = data.json();
       if (this.catquestionsOptions.status == 200) {
         //console.log("catquestionsOptions" + JSON.stringify(this.catquestionsOptions))
         this.router.navigate(['/customer/selectedService', this.catquestionsOptions.question[0].cat_code, this.catquestionsOptions.question[0].subcat_code, 'NORM']);
         this.dataServiceParent = true;
         return false;
       } else {
         this.dataServiceParent = false;
       }
     }, error => {
       this.toastr.warning('Unable to Connect to Server')
     })
 }
