   this.tempArray.push({
     mainId: data.questionsId,
     questionType: type
   })
   this.optionsArray.push(data.id)
   this.questionStdNorm.nextQuestionId = data.nextQuestionId
   this.questionStdNorm.nextQuestionType = data.nextQuestionType
   this.questionStdNorm.selectedOption = data.options
   this.questionStdNorm.selectedQuestion = question
