import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond'
import { Router, ActivatedRoute } from '@angular/router';
import { ImageUpload } from '../../../utilities/imageupload'
import { Global } from '../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
import { CustomValidators } from '../../../utilities/customValidators';

@Component({
  providers: [Beyond, ImageUpload, CustomValidators],
  selector: 'app-stepper-poc',
  templateUrl: './stepper-poc.component.html',
  styleUrls: ['./stepper-poc.component.scss']
})
export class StepperPocComponent implements OnInit {
  addAddressList: FormGroup;
  zipCodeDetails: FormGroup;
  items: any;
  randamvalue: any;
  dataZipCode: boolean = false;
  dataServiceProjectDesc: boolean = false;
  dataFormReg: boolean = false;
  dataDynamicQuestions: boolean = false;
  noMatchingProz: boolean = false;
  dataServiceSubCat: boolean = false;
  dataDummyServiceParent: boolean = false;
  dataServiceParent: boolean = false;
  showWidget: boolean = false;
  radioValue: any = 100;
  serviceResponseQuestions: any;
  tempArray: any = []
  optionsArray: any = []
  catquestionsOptions: any;
  public questionStdNorm = {
    nextQuestionId: '',
    nextQuestionType: '',
    selectedQuestion: '',
    selectedOption: '',
    id: '',
    questionId: '',
    tempId: '',
    questionType: ''
  };
  catSubCatName: any;
  categoryName: any;
  subCategoryName: any;
  constructor(public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,) { }
  ngOnInit() {
    this.beyond.beyondAdmin()
    this.catSubCatName = JSON.parse(localStorage.getItem('categoryName'))
    this.categoryName = this.catSubCatName.split("-")[0];
    this.subCategoryName = this.catSubCatName.split("-")[1];
    this.addAddressList = this.formBuilder.group({
      fromAddressValues: this.formBuilder.array([])
    })
    if (this.activatedRoute.snapshot.params['type'] == 'ZIP') {
      this.dataZipCode = true;
      this.dataDynamicQuestions = false;
      return false
    } else if (this.activatedRoute.snapshot.params['type'] == 'NORM') {
      // this.questionsFromCatgeory()
      this.dataZipCode = true;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.dataDynamicQuestions = true;
      this.dataDummyServiceParent = false;
      //console.log('The NORM')
      return false;
    } else if (this.activatedRoute.snapshot.params['type'] == 'STD') {
      //this.nextButtonQuestions()
      this.dataZipCode = false;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.dataDynamicQuestions = false;
      this.dataDummyServiceParent = false;
      this.dataServiceSubCat = true;
      //console.log('The NORM')
    }
    this.zipCodeDetails = this.formBuilder.group({
      zipCode: new FormControl("", Validators.compose([CustomValidators.zipCodeValidator])),
    })

  }

  createAddressInfo() {
    //console.log("in address info")
    return this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      userType: ['', Validators.required],
      email: ['', Validators.required],
      phoneNumber: ['', Validators.required],
      phoneType: ['', Validators.required],
      dispatchTextNumber: ['', Validators.required],
      propertyName: ['', Validators.required],
      sendInvoice: ['', Validators.required],
      serviceAddress: ['', Validators.required],
      invoiceEmail: ['', Validators.required],
      unit: ['', Validators.required],
      zipCode: ['', Validators.required],
      city: ['', Validators.required],
      state: ['', Validators.required],
      crossSt1: ['', Validators.required],
      crossSt2: ['', Validators.required],
      adminNotes: ['', Validators.required],
    });
  }


  addFieldValue() {
    this.items = this.addAddressList.get('fromAddressValues') as FormArray;
    this.nextButtonQuestions()
    this.items.push();
  }

  get addressInfoValues(): FormArray {
    return this.addAddressList.get('fromAddressValues') as FormArray;
  }

  deleteFieldValue(index: number) {
    this.addressInfoValues.removeAt(index);
  }


  zipCode(){
    this.showWidget = true;
  }


  zipcode() {
    //console.log("zipcode" + this.zipCodeDetails.value.zipCode)
    localStorage.setItem('zipcode', this.zipCodeDetails.value.zipCode)
    var val = Math.floor(1000 + Math.random() * 9000);
    this.randamvalue = "CUST" + "" + val
    //console.log("random value" + this.randamvalue)
    localStorage.setItem('customerCode', this.randamvalue)
    
    this.questionsFromCatgeory()
  }

  questionsFromCatgeory() {
    this.showWidget = true;
    //console.log("in widget"+  this.categoryName)
    //console.log("in widget"+  this.subCategoryName)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/catQuestionsOptions/PLU/PLUSC1', options)
      .subscribe(data => {
        this.catquestionsOptions = data.json();
        if (this.catquestionsOptions.status == 200) {
          //console.log("catquestionsOptions" + JSON.stringify(this.catquestionsOptions))
          this.dataServiceParent = true;
          return false;
        } else {
          this.dataServiceParent = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  nextButtonQuestions() {
    //console.log("next button question"+this.questionStdNorm.nextQuestionType)
    this.radioValue = 100;
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    if (this.questionStdNorm.nextQuestionType == "STD") {
      // this.standardQuestOption(this.questionStdNorm.nextQuestionId, this.questionStdNorm.nextQuestionType)
    } else if (this.questionStdNorm.nextQuestionType == "CAT") {
      this.http.get(Global.url + '/getNextQuestionId/' + this.questionStdNorm.nextQuestionId, options)
        .subscribe(data => {
          this.serviceResponseQuestions = data.json()
          if (this.serviceResponseQuestions.status == 200) {
            //console.log("serviceResponseQuestions" + JSON.stringify(this.serviceResponseQuestions))
            if (this.questionStdNorm.selectedQuestion == "Is this an emergency" && this.questionStdNorm.selectedOption == "Yes") {
              localStorage.setItem('emergency', JSON.stringify({
                'question': this.questionStdNorm.selectedQuestion,
                'answer': this.questionStdNorm.selectedOption
              }))
            } else if (this.questionStdNorm.selectedQuestion == "Is this an emergency" && this.questionStdNorm.selectedOption == "No") {
              localStorage.setItem('emergency', JSON.stringify({
                'question': this.questionStdNorm.selectedQuestion,
                'answer': this.questionStdNorm.selectedOption,
              }))
            } else {
              localStorage.setItem('emergency', JSON.stringify({
                'question': '',
                'answer': '',
              }))
            }

            // this.router.navigate(['/customer/selectedService', this.serviceResponseQuestions.question[0].cat_code, this.serviceResponseQuestions.question[0].subcat_code, 'STD']);
            this.dataServiceSubCat = true;
            this.dataServiceParent = true;

            // let basicobj = {
            //   customerId: localStorage.getItem('customerCode'),
            //   leadId: '' + this.basicDetails.value.leadId,
            //   serviceQuestion: this.questionStdNorm.selectedQuestion,
            //   serviceAnswer: this.questionStdNorm.selectedOption,
            //   parentId: this.questionStdNorm.nextQuestionId,
            //   mainId: this.questionStdNorm.questionId,
            //   type: this.questionStdNorm.nextQuestionType,
            //   questionType: this.questionStdNorm.questionType,
            //   tempParentId: this.tempArray,
            //   optionIds: this.optionsArray
            // }
            // localStorage.setItem('storingQuestions', JSON.stringify({
            //   'questionId': basicobj.mainId,
            //   'type': basicobj.type
            // }))

            // localStorage.setItem('tempValues', JSON.stringify({
            //   'tempArray': basicobj.tempParentId,
            // }))
            // localStorage.setItem('options', JSON.stringify({
            //   'optionIds': basicobj.optionIds

            // }))
            // this.customerServiceDetails(basicobj)
            return false;
          } else {
            this.dataServiceSubCat = false;
            return false;
          }
        }, error => {
          this.toastr.warning('Unable to Connect to Server')
        })
    }
  }

  //PARENT CHANGE
  parentChange(data, question, type) {
    //console.log('This is the parent change ' + JSON.stringify(data) + "" + question + "" + type)
    this.tempArray.push({
      mainId: data.questionsId,
      questionType: type
    })
    this.optionsArray.push(data.id)
    this.questionStdNorm.nextQuestionId = data.nextQuestionId
    this.questionStdNorm.nextQuestionType = data.nextQuestionType
    this.questionStdNorm.selectedOption = data.options
    this.questionStdNorm.selectedQuestion = question
    this.questionStdNorm.id = data.id
    this.questionStdNorm.questionId = data.questionsId
    this.questionStdNorm.questionType = type
  }
  //PARENT CHANGE
}
