  ngOnInit() {

    this.multiplewizard.Multiplewizard();
    this.imageselector.imageselector();
    this.catSubCatName = JSON.parse(localStorage.getItem('categoryName'))
    this.categoryName = this.catSubCatName.split("-")[0];
    this.subCategoryName = this.catSubCatName.split("-")[1];
    this.getListOfStates()
    //STEPPER LEVEL
    //GRAB ZIPCODE
    if (this.activatedRoute.snapshot.params['type'] == 'ZIP') {
      this.dataZipCode = true;
      this.dataDynamicQuestions = false;
      return false
    } else if (this.activatedRoute.snapshot.params['type'] == 'NORM') {
      this.questionsFromCatgeory()
      this.dataZipCode = false;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.dataDynamicQuestions = true;
      this.dataDummyServiceParent = false;
      //console.log('The NORM')
      return false;
    } else if (this.activatedRoute.snapshot.params['type'] == 'STD') {
      //this.nextButtonQuestions()
      this.dataZipCode = false;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.dataDynamicQuestions = false;
      this.dataDummyServiceParent = false;
      this.dataServiceSubCat = true;
      //console.log('The NORM')
    }
    //GRAB ZIPCODE

    //STEPPER LEVEL

    this.serviceName = this.activatedRoute.snapshot.params['serviceName']
    //CONDITIONERS
    if (this.activatedRoute.snapshot.params['id'] == 'ZIP') {
      this.dataZipCode = true;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.dataDynamicQuestions = false;
      this.dataDummyServiceParent = false;
      //console.log('The ZIP')
      return false;
    } else if (this.activatedRoute.snapshot.params['type'] == 'PD') {
      this.dataZipCode = false;
      this.dataServiceProjectDesc = true;
      this.dataFormReg = false;
      this.dataDynamicQuestions = false;
      this.dataDummyServiceParent = false;
      //console.log('The PD')
      return false;
    } else if (this.activatedRoute.snapshot.params['id'] == 'RF') {
      this.dataZipCode = false;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = true;
      this.dataDynamicQuestions = false;
      this.dataDummyServiceParent = false;
      //console.log('The RF')
      return false;
    } else if (this.activatedRoute.snapshot.params['type'] == 'NORM') {
      this.getQuestionsByParentId(this.activatedRoute.snapshot.params['id'], this.activatedRoute.snapshot.params['type'])
      this.dataZipCode = false;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.dataDynamicQuestions = true;
      this.dataDummyServiceParent = false;
      //console.log('The NORM')
      return false;
    } else if (this.activatedRoute.snapshot.params['type'] == 'STD' && this.activatedRoute.snapshot.params['id'] == 'NOPRO') {
      this.dataDummyServiceSubCat = false;
      this.dataServiceParent = false;
      this.dataDummyServiceParent = false;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.noMatchingProz = true;
      //console.log('The NOPRO')
      return false;
    } else if (this.activatedRoute.snapshot.params['type'] == 'STD') {
      this.dummyCategortByParentId(this.activatedRoute.snapshot.params['id'], this.activatedRoute.snapshot.params['type'])
      this.dataZipCode = false;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.dataDynamicQuestions = true;
      //console.log('The STD')
      return false;
    } else {
      this.dataZipCode = false;
      this.dataServiceProjectDesc = false;
      this.dataFormReg = false;
      this.dataDynamicQuestions = true;
      //console.log('The OVERALL ELSE')
      return false;
    }
    //CONDITIONERS
  }
