  //PARENT CHANGE

  previousButtonService() {
    this.questionValue = JSON.parse(localStorage.getItem('tempValues'))
    this.optionsStored = JSON.parse(localStorage.getItem('options'))

    if (this.questionValue.tempArray.length == 0) {
      this.dataZipCode = true;
      this.dataDynamicQuestions = false;
      this.router.navigate(['/customer/selectedService', this.activatedRoute.snapshot.params['cat'], this.activatedRoute.snapshot.params['subCat'], 'ZIP']);
    } else {
      // Maintaning Main ids & Temp In TempArray
      this.idarray = this.questionValue.tempArray

      // Getting last value from Array
      this.val = this.idarray[this.idarray.length - 1];

      // Getting last value from Array
      this.mainIdValue = this.val.mainId
      this.questionTypeValue = this.val.questionType

      // Removing last value from Array
      var removedVal = this.idarray.pop();

      // Updating the localstorage value
      localStorage.setItem('tempValues', JSON.stringify({
        'tempArray': this.idarray
      }))
      this.questionValue = JSON.parse(localStorage.getItem('tempValues'))

      // Maintaning Option Ids In TempArray
      this.idOption = this.optionsStored.optionIds

      // Getting last value from Array
      this.optionval = this.idOption[this.idOption.length - 1];

      // Removing last value from Array  
      var removedVal = this.idOption.pop();

      // Updating the localstorage value
      localStorage.setItem('options', JSON.stringify({
        'optionIds': this.idOption
      }))
      this.optionsStored = JSON.parse(localStorage.getItem('options'))

      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      //console.log("customerCode" + Global.url + '/deleteCategoryQuestions/' + localStorage.getItem('customerCode') + "/" + this.mainIdValue + "/" + this.questionTypeValue)
      this.http.get(Global.url + '/deleteCategoryQuestions/' + localStorage.getItem('customerCode') + "/" + this.mainIdValue + "/" + this.questionTypeValue, options)
        .subscribe(data => {
          var deleteRespone = data.json()
          if (deleteRespone.status == 200) {
            //console.log("deleteRespone" + JSON.stringify(deleteRespone))
            //console.log("deleted existing question")
          }
        })
      if (this.questionTypeValue == "STD") {
        this.previousButtonQuestions(this.mainIdValue, this.questionTypeValue)
      } else if (this.questionTypeValue == "CAT") {
        //console.log("in category function")

        this.http.get(Global.url + '/getNextQuestionId/' + this.mainIdValue, options)
          .subscribe(data => {
            this.serviceResponseQuestions = data.json()
            if (this.serviceResponseQuestions.status == 200) {
              for (var i = 0; i < this.serviceResponseQuestions.options.length; i++) {
                if (this.serviceResponseQuestions.options[i].id == this.optionval) {
                  this.radioValue = i;
                  //console.log(this.radioValue)
                  this.questionStdNorm.nextQuestionId = this.serviceResponseQuestions.options[i].nextQuestionId
                  this.questionStdNorm.nextQuestionType = this.serviceResponseQuestions.options[i].nextQuestionType
                }
              }

              if (this.questionStdNorm.selectedQuestion == "Is this an emergency" && this.questionStdNorm.selectedOption == "Yes") {
                localStorage.setItem('emergency', JSON.stringify({
                  'question': this.questionStdNorm.selectedQuestion,
                  'answer': this.questionStdNorm.selectedOption
                }))
              } else if (this.questionStdNorm.selectedQuestion == "Is this an emergency" && this.questionStdNorm.selectedOption == "No") {
                localStorage.setItem('emergency', JSON.stringify({
                  'question': this.questionStdNorm.selectedQuestion,
                  'answer': this.questionStdNorm.selectedOption
                }))
              } else {
                localStorage.setItem('emergency', JSON.stringify({
                  'question': '',
                  'answer': '',
                }))
              }
              this.dataServiceSubCat = true;
              this.dataServiceParent = false;
              return false;
            } else {
              this.dataServiceSubCat = false;
              return false;
            }
          }, error => {
            this.toastr.warning('Unable to Connect to Server')
          })
      }

    }

  }

