  // ZIPCODE API
  getZipCodesAPI(zipcode, distance, basicobj) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getZipCodes/' + zipcode + "/" + distance, options)
      .subscribe(data => {
        this.zipcodeResponse = data.json();
        //console.log("zipcodeResponse" + JSON.stringify(this.zipcodeResponse))
        if (this.zipcodeResponse.status == 200) {
          this.getMatchingProz(this.zipcodeResponse.response.zip_codes, basicobj)
          return false;
        } else {
          this.toastr.warning('No ZipCode Available...')
          this.router.navigate(['/customer/selectedService', this.activatedRoute.snapshot.params['serviceName'].trim(), 'ZIP', 'TYPE']);
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  // ZIPCODE API
  // GET MATCHING PROZ
  getMatchingProz(zipcodeResponse, basicobj) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfProzDetails', options)
      .subscribe(data => {
        var matchingProz = data.json();
        if (matchingProz.status == 200) {
          //console.log("matchingProz" + JSON.stringify(matchingProz))
          for (var i = 0; i < zipcodeResponse.length; i++) {
            for (var j = 0; j < matchingProz.response.length; j++) {
              if (matchingProz.response[j].zipCode == zipcodeResponse[i].zip_code && matchingProz.response[j].certifiedPro == "Yes") {
                //console.log("Matching" + JSON.stringify(Math.max(parseInt(matchingProz.response[j].aggregateRating))))
                this.ratingArray.push(matchingProz.response[j])
                localStorage.setItem('ratingarray', JSON.stringify(this.ratingArray))
                this.zipCodeArray.push(matchingProz.response[j].zipCode)
                this.dataDummyServiceSubCat = false;
                this.dataServiceParent = false;
                this.dataDummyServiceParent = true;
                this.dataServiceProjectDesc = false;
                this.dataFormReg = false;
                this.noMatchingProz = false;
              } else {
                //console.log("Not Matching")
                this.dataDummyServiceSubCat = false;
                this.dataServiceParent = false;
                this.dataDummyServiceParent = false;
                this.dataServiceProjectDesc = false;
                this.dataFormReg = false;
              }
              if (i == zipcodeResponse.length - 1 && j == matchingProz.response.length - 1) {
                //console.log("Strictly for UnMatching")
                if (this.zipCodeArray.length == 0) {
                  this.noMatchingProz = true;
                  //console.log("EMPTY")
                  //console.log("Strictly for UnMatching but it is approved")
                  localStorage.setItem('mainId', basicobj.mainId)
                  this.router.navigate(['/customer/selectedService', this.activatedRoute.snapshot.params['serviceName'].trim(), 'NOPRO', 'STD']);
                  return false;
                } else {
                  //console.log("NOT EMPTY")
                  //console.log("parentId" + basicobj.parentId)
                  //console.log("type" + basicobj.type)
                  this.dummyCategories()
                }

              }
            }
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
