  // Service Callings ======================================================= //



  questionsStoredMainId(parentId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/questionsStoredMain/' + this.randamvalue + '/' + parentId, options)
      .subscribe(data => {
        var questionsStored = data.json()
        if (questionsStored.status == 200) {
          this.comparisionId = questionsStored.response[0].mainId
          this.comparisionParentId = questionsStored.response[0].parentId
          this.previousParentId = questionsStored.response[0].tempParentId.split(',')[questionsStored.response[0].tempParentId.split(',').length - 1]
          this.categoryByPrevious(this.previousParentId)
        } else {
          this.dataZipCode = true;
          this.dataServiceParent = false;
          this.dataServiceSubCat = false;
          this.dataServiceProjectDesc = false;
          this.dataFormReg = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  categoryByPrevious(parentId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/questionsByParentId/' + parentId, options)
      .subscribe(data => {
        var serviceParentData = data.json();
        this.subSubServiceData = serviceParentData.response[0].question;
        var shiftingResponse = serviceParentData.response
        //console.log("shift item" + JSON.stringify(shiftingResponse.shift()))
        this.subSubServiceDataList = shiftingResponse;
        this.editMode = 1;
        for (var i = 0; i < this.subSubServiceDataList.length; i++) {
          this.subSubServiceDataList[i].checked = false;
          if (this.subSubServiceDataList[i].id == this.comparisionId) {
            this.categoryByParentIdUpdate(this.subSubServiceDataList[i].id)
            this.subSubServiceDataList[i].checked = true;
            //console.log('This is the equal id ' + this.subSubServiceDataList[i].question)
            this.selectedQuestion = this.subSubServiceDataList[i].question
            this.formsCategories()
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  categoryByParentIdUpdate(parentId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/questionsByParentId/' + parentId, options)
      .subscribe(data => {
        var parentUpdate = data.json()
        //console.log('This is parent update ' + JSON.stringify(parentUpdate))
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  updateCategories() {
    //console.log('This is update category ' + this.sCategoriesDetails.value.subCategories)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/questionsById/' + this.sCategoriesDetails.value.subCategories, options)
      .subscribe(data => {
        var updateData = data.json()
        if (updateData.status == 200) {
          let basicobj = {
            customerId: this.randamvalue,
            serviceAnswer: updateData.response[0].question,
            mainId: this.sCategoriesDetails.value.subCategories,
            parentId: this.comparisionParentId
          }
          this.updateCategoriesAPI(basicobj)
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  updateCategoriesAPI(updateObj) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.post(Global.url + '/updateQuestionsById', updateObj, options)
      .subscribe(data => {
        var updateData = data.json()
        if (updateData.status == 200) {
          this.editMode = 0;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  formsCategories() {
    this.sCategoriesDetails = this.formBuilder.group({
      subCategories: [this.comparisionId, Validators.required],
      serviceQuestion: [this.subSubServiceData, Validators.required],
      questionResponse: [this.selectedQuestion, Validators.required],
    })
  }



