  mobileNumberVerification() {
    if (this.basicDetails.value.phone == "") {
      this.toastr.warning("Phone is Empty")
      this.error = false;
      this.disableCall = true;
      return false;
    }
    this.http.get(Global.url + '/customerPhoneVerification/' + this.basicDetails.value.phone)
      .subscribe(data => {
        var response = data.json();
        //console.log("response" + JSON.stringify(response))
        if (response.status == "TRUE") {
          this.error = true;

        } else {
          this.error = false;
          this.disableCall = false;
        }
      })
  }

  mailIdVerification() {
   
    if (this.basicDetails.value.emailId == "") {
      this.toastr.warning("Email Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    }
    this.http.get(Global.url + '/newCustomerMailIdVerification/' + this.basicDetails.value.emailId + "/" + this.role)
      .subscribe(data => {
        var mailResponse = data.json();
        //console.log("response" + JSON.stringify(mailResponse))
        if (mailResponse.status == "TRUE") {
          this.authStatus = 1;
          this.disableSignIn = false;
          this.mailError = true;
        } else {
          this.authStatus = 0;
          this.mailError = false;
          this.disableSignIn = true;
        }
      })
  }

  signin() {
    this.router.navigate(['/auth/login', this.authStatus])
  }

  clickToCall() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/click2Call/' + this.basicDetails.value.phone, options)
      .subscribe(data => {
        var response = data.json();
        this.clickToCallData = response.response;
        if (response.status == 200) {
          this.toastr.success('Your Call is forwarding')
        }
        //console.log("This is Response: " + JSON.stringify(response))
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

