    var leadRef5 = new Date().getMinutes();
    var leadRef6 = new Date().getSeconds();
    this.leadRef = leadRef1 + "" + leadRef2 + "" + leadRef3 + "" + leadRef4 + "" + leadRef5 + "" + leadRef6
