  //PARENT CHANGE
  parentChange(data, question, id, type) {
    console.log('This is the parent change ' + JSON.stringify(data) + "" + question + "" + id + "" + type)
    this.questionStdNorm.nextQuestionId = data.nextQuestionId
    this.questionStdNorm.nextQuestionType = data.nextQuestionType
    this.questionStdNorm.selectedOption = data.options
    this.questionStdNorm.selectedQuestion = question
    this.questionStdNorm.id = data.id
    this.questionStdNorm.questionId = data.questionsId
    this.questionStdNorm.questionType = type
  }
  //PARENT CHANGE

  parentChange1(dropdowndata, Question, QuestionId, event) {
    //console.log(event.target.value)
    let obj = {
      options: '' + dropdowndata,
      questionsId: '' + QuestionId,
    }
    //console.log("obj"+JSON.stringify(obj))
    this.http.post(Global.url + '/getQuestionOptionForDropdown', obj)
      .subscribe(data => {
        var response = data.json();
        //console.log("response"+JSON.stringify(response))
        this.dropdownData = response.response[0];
        //console.log("dropdownData" + JSON.stringify(this.dropdownData))
        this.questionStdNorm.nextQuestionId = this.dropdownData.nextQuestionId
        this.questionStdNorm.nextQuestionType = this.dropdownData.nextQuestionType
        this.questionStdNorm.selectedOption = this.dropdownData.options
        this.questionStdNorm.selectedQuestion = Question
        this.questionStdNorm.id = this.dropdownData.id
        this.questionStdNorm.questionId = this.dropdownData.questionsId
        this.questionStdNorm.questionType = this.dropdownData.nextQuestionType
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })

  }

