  //BASIC DETAILS STORING
  basicDetailsFormSubmition() {
    var emergency = JSON.parse(localStorage.getItem('emergency'))
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    if (emergency.question == "" && emergency.answer == "") {
      ////console.log("emergency question not there")
      this.basicDetailsAPI("NOSEND");
    } else {
      this.http.get(Global.url + '/getEmergencyTag/' + localStorage.getItem('customerCode') + "/" + emergency.question + "/" + emergency.answer, options)
        .subscribe(data => {
          var emergencyResponse = data.json()
          ////console.log("emergency response" + JSON.stringify(emergencyResponse))
          if (emergencyResponse.status == 200) {
            if (emergencyResponse.response[0].serviceQuestion == "Is this an emergency" && emergencyResponse.response[0].serviceAnswer == "Yes") {
              ////console.log("emergencyResponse in if")
              this.basicDetailsAPI("ISSEND");
            } else {
              ////console.log("emergencyResponse in else")
              this.basicDetailsAPI("NOSEND");
            }
          } else {

          }
        }, error => {
          this.toastr.warning('Unable to Connect to Server')
        })

    }
  }
  //BASIC DETAILS STORING
  // BASIC DETAILS API
  basicDetailsAPI(type) {
    this.spinner.show()
    var emergency = JSON.parse(localStorage.getItem('emergency'))
    var addressMixed = this.basicDetails.value.street + "," + this.basicDetails.value.city + "," + this.basicDetails.value.state + "," + localStorage.getItem('zipcode')
    this.geocodingService.getLatLongViaZipcode(addressMixed)
      .subscribe(address => {
        console.log(address)
        if (address == undefined || address == '') {
          this.coordinates.latitude = 0
          this.coordinates.longitude = 0
        } else {
          var coordObj = {
            lat: address.lat,
            lng: address.lng,
          }
          this.coordinates.latitude = address.lat
          this.coordinates.longitude = address.lng
        }
        let basicobj = {
          "customerId": localStorage.getItem('customerCode'),
          "leadId": '' + this.leadRef,
          "dateCreated": `${Utility.year}-${Utility.month}-${Utility.day} ${Utility.hour}:${Utility.min}:${Utility.seconds}`,
          "customerStatus": '' + this.basicDetails.value.customerStatus,
          "firstName": '' + this.basicDetails.value.firstName,
          "lastName": '' + this.basicDetails.value.lastName,
          "phone": '' + this.basicDetails.value.phone,
          "emailId": '' + this.basicDetails.value.emailId,
          "street": '' + this.basicDetails.value.street,
          "state": '' + this.basicDetails.value.state,
          "city": '' + this.basicDetails.value.city,
          "zipCode": '' + localStorage.getItem('zipcode'),
          "serviceStreet": '' + this.basicDetails.value.street,
          "serviceState": '' + this.basicDetails.value.state,
          "serviceCity": '' + this.basicDetails.value.city,
          "serviceZipCode": '' + localStorage.getItem('zipcode'),
          "latitude": '' + this.coordinates.latitude,
          "longitude": '' + this.coordinates.longitude,
          "projectDescription": '' + this.projectDescriptionDetails.value.projectDescription,
          "image": '' + Global.imagePath,
          "documentName": '' + Global.documentName,
          "serviceName": this.categoryName,
          "subCategoryName": this.subCategoryName,
          "catcode": this.activatedRoute.snapshot.params['cat'],
          "subcatcode": this.activatedRoute.snapshot.params['subCat'],
          "leadcost": this.perleadCost,
          "companyName": '',
          "aggregateRating": '',
          "emergency": emergency.answer,
          "type": type,
          "customerImage": '' + this.customerImageData

        }
        console.log("customerImageData" + JSON.stringify(basicobj))
        this.http.post(Global.url + '/addLeadCustomer', basicobj)
          .subscribe(data => {
            var response = data.json();
            ////console.log(JSON.stringify(response))
            if (response.status === 200) {
              var customerId = localStorage.getItem('customerCode')
              ////console.log("this is CustomerId: " + JSON.stringify(customerId))
              this.toastr.success("Lead Data Saved Successfully...! Please check your mail that you have entered...!")
              localStorage.setItem('response', JSON.stringify(response))
              this.spinner.hide()
              window.location.href = '/customer/leadOtpConfirmation/' + customerId
            }
          }, error => {
            this.toastr.warning('Unable to connect to Server');
          })
      })

  }
