  getListOfStates() {
    // this.zipcodeValue = JSON.parse(localStorage.getItem('zipcode'))
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  mailIdVerification() {
    //console.log("in mail id verification"+this.role)
    //console.log("in mail id verification"+this.basicDetails.value.emailId)
    if (this.basicDetails.value.emailId == "") {
      this.toastr.warning("Email Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    }
    this.http.get(Global.url + '/newCustomerMailIdVerification/' + this.basicDetails.value.emailId + "/" + this.role)
      .subscribe(data => {
        var mailResponse = data.json();
        //console.log("response" + JSON.stringify(mailResponse))
        if (mailResponse.status == "TRUE") {
          this.authStatus = 1;
          this.disableSignIn = false;
          this.mailError = true;
        } else {
          this.authStatus = 0;
          this.mailError = false;
          this.disableSignIn = true;
        }
      })
  }


  signin() {
    // this.router.navigate(['/auth/login', this.authStatus])
    this.router.navigate(['auth/login/0'])
  }

  getLeadCost() {
    this.http.get(Global.url + '/getLeadCost/' + this.activatedRoute.snapshot.params['cat'] + "/" + this.activatedRoute.snapshot.params['subCat'])
      .subscribe(data => {
        var lead = data.json();

        if (lead.status == 200) {
          this.leadsData = lead.response[0];
          this.perleadCost = this.leadsData.perLead;
          console.log("leadCost" + JSON.stringify(this.leadsData))
        }
        else {
          console.log("No Records Found")
        }

      })
  }



