import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../utilities/imageupload'
import { Global } from '../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond,],
  selector: 'app-customer-my-jobs',
  templateUrl: './customer-my-jobs.component.html',
  styleUrls: ['./customer-my-jobs.component.scss']
})
export class CustomerMyJobsComponent implements OnInit {
  estimate1: boolean = false;
  inprogress: boolean = false;
  complete1: boolean = false;
  loginData: any;
  loginCode: any;
  leadDetails: any;
  leadResponse: boolean;
  logData: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService) {

  }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem('login'))
    this.loginCode = this.loginData.loginCode
    // this.estimateList()
    this.getListOfLeads()
  }
  getListOfLeads() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadDetails' + '/' + this.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.leadDetails = response.response;
          //console.log("estimateListDetails" + JSON.stringify(this.estimateListDetails))
          this.leadResponse = true;
        }
        else {
          this.leadResponse = false;
        }
        error => {
        }
      })

    // estimate(){
    //   this.estimate1 = true
    //   this.inprogress = false
    //   this.complete1 = false
    // }
    // inProgress() {
    //   this.inprogress = true
    //   this.estimate1 = false
    //   this.complete1 = false
    // } 
    // complete(){
    //   this.complete1 = true
    //   this.estimate1 = false
    //   this.inprogress = false
    // }
  }
  addCustomer() {
    this.router.navigate(['/customer/addNewJob'])
  }
  editCustomer(customerId) {
    alert('Requirement has to be given by Tim')
  }
  deleteCustomer() {
    alert('Coming Soon')
  }
  viewLead(customerId, leadId) {
    console.log("customerId" + customerId + leadId)
    this.router.navigate(['/customer/customerMyjobsView', customerId, leadId])
  }
  home(){
    this.router.navigate(['/customer/customerDashboard', this.loginData.loginCode])
  }

}
