import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { delay } from 'q';
import { multiplewizard } from '../../../utilities/multiplewizard';
import { Imageselector } from 'src/utilities/imageselector';

import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Global } from '../../../global/global';

@Component({
  providers: [Beyond, multiplewizard, Imageselector],
  selector: 'app-lead-edit-profile',
  templateUrl: './lead-edit-profile.component.html',
  styleUrls: ['./lead-edit-profile.component.scss']
})
export class LeadEditProfileComponent implements OnInit {
  customerID: string;
  customerStatus: boolean;
  customerData: any;
  image: any;
  customerData2: string;
  leadStatus: any;
  zipCode: any;
  editLeadForm: FormGroup
  imagePreview: string = "../../assets/img/avatars/default_person.jpg"
  CurrentUserDetails: any;
  percentage: number;
  progressHide: boolean;
  progressShow: boolean;
  img1: boolean;
  file2Upload: string | Blob;
  img: boolean;
  loginData: any;
  logData: any;
  loginCode: any;
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.logData = JSON.parse(localStorage.getItem("login"));
    this.loginCode =  this.logData.loginCode
    this.CurrentUserDetails = JSON.parse(localStorage.getItem('login'))
    console.log("CurrentUserDetails " + JSON.stringify(this.CurrentUserDetails))
    console.log("loginData" + JSON.stringify(this.CurrentUserDetails.loginCode))
    this.customerID = this.CurrentUserDetails.loginCode,
      console.log("customerID" + JSON.stringify(this.customerID))
    this.listLeadCustomer()
    this.editLeadForm = this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      phone: ['', Validators.required],
      state: ['', Validators.required],
      city: ['', Validators.required],
      emailId: ['', Validators.required],
      street: ['', Validators.required],
      zipCode: ['', Validators.required],
      categoryCode: ['', Validators.required],
      customerImage: ['', Validators.required],
    })
  }
  listLeadCustomer() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewLeadGenCustomerData/' + this.customerID, options)
      .subscribe(data => {
        var leadCustomerData = data.json();
        if (leadCustomerData.status == 200) {
          this.customerStatus = true;
          this.customerData = leadCustomerData.response[0];
          console.log("THIS IS LIST OF LEAD DATA" + JSON.stringify(this.customerData))
          this.image = this.customerData.customerImage

          if (this.customerData.customerImage == '' || this.customerData.customerImage == null) {

            this.imagePreview = "../../assets/img/avatars/default_person.jpg"
          }
          else if (this.customerData.customerImage == "default_person.jpg") {
            this.imagePreview = "../../assets/img/avatars/default_person.jpg"
         }
           else {
            this.imagePreview = Global.url + "/" + this.customerData.customerImage

          }
          this.editData(this.customerData)


          this.leadStatus = this.customerData.leadStatus
          this.zipCode = this.customerData.zipCode
          console.log("in lead cust" + JSON.stringify(this.customerData.customerImage))
        } else {
          this.customerStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  editData(x) {
    console.log("EDIT DATA" + JSON.stringify(x))
    this.editLeadForm = this.formBuilder.group({
      customerId: [x.customerId, Validators.required],
      firstName: [x.firstName, Validators.required],
      lastName: [x.lastName, Validators.required],
      phone: [x.phone, Validators.required],
      emailId: [x.emailId, Validators.required],
      city: [x.city, Validators.required],
      state: [x.state, Validators.required],
      zipCode: [x.zipCode, Validators.required],
      street: [x.street, Validators.required],

    })

  }
  editButton() {
    let obj = {
      "customerId": "" + this.customerID,
      "firstName": '' + this.editLeadForm.value.firstName,
      "lastName": '' + this.editLeadForm.value.lastName,
      "phone": '' + this.editLeadForm.value.phone,
      "emailId": '' + this.editLeadForm.value.emailId,
      "street": '' + this.editLeadForm.value.street,
      "city": '' + this.editLeadForm.value.city,
      "state": '' + this.editLeadForm.value.state,
      "zipCode": '' + this.editLeadForm.value.zipCode,
      "customerImage": '' + Global.documentName,
    }
    this.http.post(Global.url + '/editLeadGenCustomerData/' + this.customerID, obj)
      .subscribe(data => {
        var leadProfileInfo = data.json();
        if (leadProfileInfo.status == 200) {
          //console.log("lead Profile Info " + JSON.stringify(leadProfileInfo))
          this.toastr.success('Lead Profile Updated Successfully...');
          this.router.navigate(['/customer/leadViewMyprofile'])
        } else {
          this.toastr.warning("Failed to Update")
        }
      }, error => {
        //console.log(error);// Error getting the data
      });

  }
  imgfileUpload() {
    console.log("imgfileUpload calling")
    const url = Global.url + "/imageUploadData";
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append("image", this.file2Upload);
    const config = {
      headers: {
        "content-type": "multipart/form-data"
      }
    };
    xhr.open("post", url, true);
    xhr.upload.onprogress = e => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage === 100) {
          this.progressShow = false;
          this.progressHide = true;
          // this.imageUploadVariable.nativeElement.value = "";
          this.img1 = false;
        }
      }
    };
    xhr.onerror = function (e) { };
    xhr.onload = function () {
      var a = JSON.parse(xhr.responseText);
      Global.secimagePath = a.imagePath;
      Global.documentName = a.name;

      console.log("GLObaL NAme" + Global.documentName)
    };
    xhr.send(formData);
  }
  //IMAGE UPLOAD
  handleImage(file) {
    var picReader = new FileReader();
    picReader.onload = (e: any) => {
      var reader = e.target;
      this.imagePreview = reader.result;
    }
    picReader.readAsDataURL(file)
  }

  select(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.img = true;
      this.progressShow = false;
      this.progressHide = false;
      this.file2Upload = event.target.files[0];
      this.handleImage(file)
    } else {
      this.img = false;
      this.progressShow = false;
      this.progressHide = false;
      this.imagePreview = '../../assets/img/no-image.png'
    }
  }
okButton(){
  this.router.navigate(['customer/leadViewMyprofile'])
}
home()
{
  this.router.navigate(['/customer/customerDashboard', this.logData.loginCode])
}
list(){
  this.router.navigate(['/customer/customerMyJobs'])
}
}

