import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../utilities/beyond';
import { delay } from 'q';
import { multiplewizard } from '../../../utilities/multiplewizard';
import { Imageselector } from 'src/utilities/imageselector';

import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Global } from '../../../global/global';
import { CustomValidators } from '../../../utilities/customValidators';

@Component({
  providers: [Beyond, multiplewizard, Imageselector, CustomValidators],
  selector: 'app-select-proz',
  templateUrl: './select-proz.component.html',
  styleUrls: ['./select-proz.component.css']
})
export class SelectProzComponent implements OnInit {
  prozDetails: FormGroup;
  prozData: any;
  listOfProzData: any;
  tempServiceName: any = [];
  tempServiceAggregate: any = [];
  tempBusinessSince: any = [];
  serviceName: any = [];
  serviceAggregate: any = [];
  inBusinessSince: any = [];
  customerId: any;
  leaddCustomerData: any;
  customerData: any;
  customerStatus: boolean = false;
  customerHistoryStatus: boolean = false;
  getQuote: boolean = false;
  getProz: boolean = false;
  customerHistoryData: any;
  emergency: any;
  theEmergencyRequest:boolean = false;
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) {
    this.prozDetails = this.formBuilder.group({
      companyName: ['', Validators.required],
      aggregateRating: ['', Validators.required],
      serviceCost: ['', Validators.required],
    })
  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.multiplewizard.Multiplewizard();
    this.imageselector.imageselector();
    this.listLeadCustomerHistory(this.activatedRoute.snapshot.params['anyCode'])
    this.listOfProzs();
  }

  listOfProzs() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/prozDetails', options)
      .subscribe(data => {
        this.prozData = data.json();
        this.listOfProzData = this.prozData.response;
        //console.log("hi..."+JSON.stringify(this.listOfProzData))
        for (var i = 0; i < this.listOfProzData.length; i++) {
          this.listOfProzData[i].checked = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  selectBtn() {
    let selectObj = {
      "customerId": this.activatedRoute.snapshot.params['anyCode'],
      "companyName": this.serviceName,
      "aggregateRating": this.serviceAggregate,
      "inBusinessSince": this.inBusinessSince
    }
    //console.log("hi obj"+JSON.stringify(selectObj))
    this.http.post(Global.url + '/updateLeadCustomer', selectObj)
      .subscribe(data => {
        var response = data.json();
        //console.log("response in get quote"+JSON.stringify(response))
        if (response.status === 200) {
          this.router.navigate(['/customer/leadConfirmation', this.activatedRoute.snapshot.params['anyCode']])
          this.toastr.success("Lead Data Saved Successfully...!")
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  company(data) {
    if (data.checked == true) {
      this.tempServiceName.push({
        id:data.id,
        companyName:data.companyName
      })
      this.tempServiceAggregate.push({
        id:data.id,
        aggregateRating:data.aggregateRating
      })
      this.tempBusinessSince.push({
        id:data.id,
        inBusinessSince:data.inBusinessSince
      })
      this.serviceName.push(data.companyName)
      this.serviceAggregate.push(data.aggregateRating)
      this.inBusinessSince.push(data.inBusinessSince)
      //console.log("serviceName: "+JSON.stringify(this.serviceName))
    } else {
      var tempService = [];
      var tempAggregate = [];
      var tempBusinessSince = [];
      var service = [];
      var aggregate = [];
      var businessSince = [];
      for (var i = 0; i < this.tempServiceName.length; i++) {
        if (this.tempServiceName[i].id != data.id) {
          tempService.push(this.tempServiceName[i])
          service.push(this.tempServiceName[i].companyName)
        }
      }
      for (var i = 0; i < this.tempServiceAggregate.length; i++) {
        if (this.tempServiceAggregate[i].id != data.id) {
          tempAggregate.push(this.tempServiceAggregate[i])
          aggregate.push(this.tempServiceAggregate[i].aggregateRating)
        }
      }      
      for (var i = 0; i < this.tempBusinessSince.length; i++) {
        if (this.tempBusinessSince[i].id != data.id) {
          tempBusinessSince.push(this.tempBusinessSince[i])
          businessSince.push(this.tempBusinessSince[i].aggregateRating)
        }
      }      
      this.tempServiceName = []
      this.tempServiceAggregate = []
      this.tempBusinessSince = []
      this.serviceName = [];
      this.serviceAggregate = [];
      this.inBusinessSince = [];
      this.tempServiceName = tempService
      this.tempServiceAggregate = tempAggregate
      this.tempBusinessSince = tempBusinessSince
      this.serviceName = service
      this.serviceAggregate = aggregate
      this.inBusinessSince = businessSince
      
    }
  }

  /* View Profile */
  viewProzProfile(data){
    localStorage.setItem('data', JSON.stringify(data))
    window.open("http://localhost:4300/customer/viewProzProfile", "_blank")  
  }
  /* /View Profile */


  listLeadCustomerHistory(customerId) {
    //console.log("hi customer"+customerId)
      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.get(Global.url + '/getLeadHistoryDetails/' + customerId,  options)
        .subscribe(data => {
          var leadCustomerHistoryData = data.json();
          if (leadCustomerHistoryData.status == 200) {
            this.customerHistoryStatus = true;
            this.customerHistoryData = leadCustomerHistoryData.response;
           for(var i=0; i<this.customerHistoryData.length; i++){
            if (this.customerHistoryData[i].serviceQuestion == "Is this an emergency" && this.customerHistoryData[i].serviceAnswer == "Yes") {
              //console.log("in color if"+this.customerHistoryData[i].serviceQuestion)
              this.customerHistoryData[i].color = 'red';
              this.customerHistoryData[i].colorRed = true;
              this.theEmergencyRequest = true;
            }else if(this.customerHistoryData[i].serviceQuestion == "Is this an emergency" && this.customerHistoryData[i].serviceAnswer == "No"){
              //console.log("in color if else"+this.customerHistoryData[i].serviceQuestion)
              this.customerHistoryData[i].color = 'green';
              this.customerHistoryData[i].colorRed = false;
              this.theEmergencyRequest = false;
            }
           }
            //console.log("customer data"+JSON.stringify(this.customerData))
          } else {
            this.customerHistoryStatus = false;
          }
        }, error => {
          this.toastr.warning('Unable to Connect to Server')
        })
  }



}
