import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global';
import { AuthService } from '../../../auth/auth.service'
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  providers: [Beyond],
  selector: 'app-list-estimates',
  templateUrl: './list-estimates.component.html',
  styleUrls: ['./list-estimates.component.scss']
})
export class ListEstimatesComponent implements OnInit {
  searchEstimate:FormGroup;
  logData:any;
  loginCode:any;
  estimateData:any;
  estimateBasicDetails:any;
  estimateLabourList:any;
  estimateCustomMaterialList:any;
  estimateListDetails: any;
  estimateListResponse: boolean;
  loginData: any;
  constructor( 
    public beyond:Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
    ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.logData= JSON.parse(localStorage.getItem('login'))
    this.loginCode =  this.logData.loginCode
    // this.estimateList()
    this.getListOfEstimates()
  }
  getListOfEstimates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfEstimatesForCustomers' + '/' + this.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.estimateListDetails = response.response;
          //console.log("estimateListDetails" + JSON.stringify(this.estimateListDetails))
          this.estimateListResponse = true;
        }
        else {
          this.estimateListResponse = false;
        }
        error => {
        }
      })
  }
  
  viewEstimate(estimateNo){
    this.router.navigate(['/customer/viewEstimate/',estimateNo])
  }
  home(){
    this.router.navigate(['/customer/customerDashboard', this.logData.loginCode])
  }
}
