import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CustomerLayoutComponent } from '../customer/customer-layout/customer-layout.component';
import { LeadConfirmationComponent } from '../customer/lead-confirmation/lead-confirmation.component';
import { LeadGenerationComponent } from '../customer/lead-generation/lead-generation.component';
import { LeadOtpConfirmationComponent } from '../customer/lead-otp-confirmation/lead-otp-confirmation.component';
import { LeadPreviewComponent } from '../customer/lead-preview/lead-preview.component';
import { SelectProzComponent } from '../customer/select-proz/select-proz.component';
import { ViewProzProfileComponent } from '../customer/view-proz-profile/view-proz-profile.component';
import { CustomerDashboardComponent } from '../customer/customer-dashboard/customer-dashboard.component';
import { ListLeadsTaComponent } from '../customer/list-leads-ta/list-leads-ta.component';
import { StepperPocComponent } from './stepper-poc/stepper-poc.component';
import { TestingLeadGenComponent } from './testing-lead-gen/testing-lead-gen.component'
import { StepperDynamicComponent } from './stepper-dynamic/stepper-dynamic.component'
import { CustomerMyJobsComponent } from './customer-my-jobs/customer-my-jobs.component';
import { ViewEstimateComponent } from './customer-estimates/view-estimate/view-estimate.component';
import { ListEstimatesComponent } from './customer-estimates/list-estimates/list-estimates.component'
import { ViewInvoiceComponent } from './customer-Invoices/view-invoice/view-invoice.component';
import { ListInvoiceComponent } from './customer-Invoices/list-invoice/list-invoice.component';
import { FeedbackComponent } from './feedback/feedback.component';
import { CustomerInvoiceAccountsettingsComponent } from './customer-Invoices/customer-invoice-accountsettings/customer-invoice-accountsettings.component';
import { CustomerViewMyJobsComponent } from './customer-view-my-jobs/customer-view-my-jobs.component';
import { AddNewJobComponent } from './add-new-job/add-new-job.component';
import { VanlynkCustomerMyProfileComponent } from './vanlynk-customer-my-profile/vanlynk-customer-my-profile.component';
import { VanlynkCustomerEditProfileComponent } from './vanlynk-customer-edit-profile/vanlynk-customer-edit-profile.component';
import { LeadEditProfileComponent } from './lead-edit-profile/lead-edit-profile.component';
import { LeadMyProfileComponent } from './lead-my-profile/lead-my-profile.component'
import { CustomerNotificationComponent } from './customer-notification/customer-notification.component';
const routes: Routes = [
  {
    path: '', component: CustomerLayoutComponent,
    children: [

      {
        path: 'leadConfirmation/:anyCode',
        component: LeadConfirmationComponent
      },
      {
        path: 'customerMyJobs',
        component: CustomerMyJobsComponent
      },
      {
        path: 'customerMyjobsView/:customerId/:leadId',
        component: CustomerViewMyJobsComponent
      },

      {
        path: 'selectedService/:cat/:subCat/:type',
        component: StepperDynamicComponent
        // component: LeadGenerationComponent 
      },
      {
        path: 'leadOtpConfirmation/:anyCode',
        component: LeadOtpConfirmationComponent
      },
      {
        path: 'leadPreview/:anyCode',
        component: LeadPreviewComponent
      },
      {
        path: 'selectProz/:anyCode',
        component: SelectProzComponent
      },
      {
        path: 'viewProzProfile',
        component: ViewProzProfileComponent
      },
      {
        path: 'customerDashboard/:anyCode',
        component: CustomerDashboardComponent
      },
      {
        path: 'listLeads',
        component: ListLeadsTaComponent
      },
      {
        path: 'stepperPoc',
        component: StepperPocComponent
      },
      {
        path: 'stepperDynamic',
        // component: StepperDynamicComponent 
      },
      {
        path: 'testingLeadGen/:cat/:subCat/:type',
        component: TestingLeadGenComponent
      },
      {
        path: 'viewEstimate/:estimateNo',
        component: ViewEstimateComponent
      },
      {
        path: 'listEstimates',
        component: ListEstimatesComponent
      },
      {
        path: 'listInvoices',
        component: ListInvoiceComponent
      },
      {
        path: 'viewInvoice/:invoiceNo',
        component: ViewInvoiceComponent
      },
      {
        path: 'feedback',
        component: FeedbackComponent
      },
      {
        path: 'customerInvoiceAccountsettings/:invoiceNo',
        component: CustomerInvoiceAccountsettingsComponent
      },
      {
        path: 'vanlynkCustomerMyprofileView',
        component: VanlynkCustomerMyProfileComponent,

      },
      {
        path: 'vanlynkCustomerMyprofileEdit',
        component: VanlynkCustomerEditProfileComponent
      },
      {
        path: 'addNewJob',
        component: AddNewJobComponent
      },
      {
        path: 'leadEditMyprofile',
        component: LeadEditProfileComponent
      },
      {
        path: 'leadViewMyprofile',
        component: LeadMyProfileComponent
      },
      {
        path: 'notification',
        component: CustomerNotificationComponent
      },
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CustomerRoutingModule { }
