import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../utilities/beyond';
import { delay } from 'q';
import { multiplewizard } from '../../../utilities/multiplewizard';
import { Imageselector } from 'src/utilities/imageselector';

import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Global } from '../../../global/global';
import { CustomValidators } from '../../../utilities/customValidators';

@Component({
  providers: [Beyond, multiplewizard, Imageselector, CustomValidators],
  selector: 'app-lead-preview',
  templateUrl: './lead-preview.component.html',
  styleUrls: ['./lead-preview.component.css']
})
export class LeadPreviewComponent implements OnInit {
  customerId: any;
  leaddCustomerData: any;
  customerData: any;
  serviceName: any;
  customerStatus: boolean = false;
  customerHistoryStatus: boolean = false;
  matchingStatus: boolean = false;
  getQuote: boolean = false;
  getProz: boolean = false;
  customerHistoryData: any;
  emergency: any;
  catSubCatName: any;
  categoryName: any;
  subCategoryName: any;
  matchingProData: any;
  zipCode: any;
  theEmergencyRequest:boolean = false;
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.multiplewizard.Multiplewizard();
    this.imageselector.imageselector();

    this.catSubCatName = JSON.parse(localStorage.getItem('categoryName'))
    this.categoryName = this.catSubCatName.split("-")[0];
    this.subCategoryName = this.catSubCatName.split("-")[1];

    this.listLeadCustomerHistory(this.activatedRoute.snapshot.params['anyCode'])
    this.emergency = JSON.parse(localStorage.getItem('emergency'))
    this.listLeadCustomer(this.activatedRoute.snapshot.params['anyCode'])
    if(this.emergency.question == "Is this an emergency" && this.emergency.answer == "Yes"){
      this.getQuote = true;
      this.getProz = false;
      //this.getMatchingPros();
      // this.getProz = true;
      // this.getQuote = false;
      return false;
    }else if(this.emergency.question == "Is this an emergency" && this.emergency.answer == "No"){
      // this.getProz = true;
      // this.getQuote = false;
      this.getQuote = true;
      this.getProz = false;
      return false;
    }
  }

  listLeadCustomerHistory(customerId) {
    //console.log("hi customer"+customerId)
      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.get(Global.url + '/getLeadHistoryDetails/' + customerId,  options)
        .subscribe(data => {
          var leadCustomerHistoryData = data.json();
          if (leadCustomerHistoryData.status == 200) {
            this.customerHistoryStatus = true;
            this.customerHistoryData = leadCustomerHistoryData.response;            
           for(var i=0; i<this.customerHistoryData.length; i++){
            if (this.customerHistoryData[i].serviceQuestion == "Is this an emergency" && this.customerHistoryData[i].serviceAnswer == "Yes") {
              //console.log("in color if"+this.customerHistoryData[i].serviceQuestion)
              this.customerHistoryData[i].color = 'red';
              this.customerHistoryData[i].colorRed = true;
              this.theEmergencyRequest = true;
              
            }else if(this.customerHistoryData[i].serviceQuestion == "Is this an emergency" && this.customerHistoryData[i].serviceAnswer == "No"){
              //console.log("in color if else"+this.customerHistoryData[i].serviceQuestion)
              this.customerHistoryData[i].color = 'green';
              this.customerHistoryData[i].colorRed = false;
              this.theEmergencyRequest = false;
            }
           }
            //console.log("customer data"+JSON.stringify(this.customerData))
          } else {
            this.customerHistoryStatus = false;
          }
        }, error => {
          this.toastr.warning('Unable to Connect to Server')
        })
  }
  
  listLeadCustomer(customerId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadDetails/' + customerId, options)
      .subscribe(data => {
        var leadCustomerData = data.json();
        if (leadCustomerData.status == 200) {
          this.customerStatus = true;
          this.customerData = leadCustomerData.response[0];
          this.zipCode  = this.customerData.zipCode
          //console.log("in lead cust"+JSON.stringify(this.customerData.zipCode))
        } else {
          this.customerStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  viewMatchingProzs() {
    if(this.emergency.question == "Is this an emergency" && this.emergency.answer == "Yes"){
      this.getQuote = true;
      this.getProz = false;
      this.getMatchingPros()
      this.router.navigate(['/customer/leadConfirmation', this.activatedRoute.snapshot.params['anyCode']])
      return false;
    }else if(this.emergency.question == "Is this an emergency" && this.emergency.answer == "No"){
      this.getQuote = true;
      this.getProz = false;
      this.getMatchingPros()
      this.router.navigate(['/customer/leadConfirmation', this.activatedRoute.snapshot.params['anyCode']])
      return false;
    }
  }

  

  cancelBtn() {
    var deleteObj = {
      customerId:this.activatedRoute.snapshot.params['customerId']
    }
    this.http.post(Global.url + '/deleteCustomerDetails', deleteObj)
      .subscribe(data => {
        var response = data.json();
        if (response.status === 200) {
          this.router.navigate(['/topProzHome'])
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }


  getMatchingPros() {
    //console.log("zipcode"+this.zipCode)
    //console.log("categoryName"+this.categoryName)
    //console.log("subCategoryName"+this.subCategoryName)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getMatchingpros/' + this.zipCode + "/" + this.categoryName + "/" + this.subCategoryName, options)
      .subscribe(data => {
        var matchingProsData = data.json();
        //console.log("matching pro data"+JSON.stringify(matchingProsData))
        if (matchingProsData.status == 200) {
          this.matchingStatus = true;
          this.matchingProData = matchingProsData.response;
          alert("Matching Pros Data"+JSON.stringify(this.matchingProData))
        } else {
          this.matchingStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

}
