  questionsFromCatgeory(catcode, subCatCode) {
    console.log("catcode", catcode, subCatCode)
    $('html, body').animate({
      scrollTop: $('#footer').offset().top
    }, 1000);
    // console.log("in cat quest")
    this.showWidget = true;
    this.buttonEnable = false;
    this.buttonDisable = false;
    this.prevButton = false
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/catQuestionsOptions/' + catcode + "/" + subCatCode, options)
      .subscribe(data => {
        this.catquestionsOptions = data.json();
        console.log("catquestionsOptions" + JSON.stringify(this.catquestionsOptions))
        if (this.catquestionsOptions.status == 200) {
          console.log("catquestionsOptions" + JSON.stringify(this.catquestionsOptions))
          this.dataServiceParent = true

          return false;
        } else {
          this.dataServiceParent = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  nextButtonQuestions(count, divData) {
    $('html, body').animate({
      scrollTop: $('#footer').offset().top
    }, 1000);
    console.log("NextQuestionType" + this.questionStdNorm.nextQuestionType)
    console.log("NextQuestionId" + this.questionStdNorm.nextQuestionId)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    if (this.questionStdNorm.nextQuestionType == "PD") {
      let basicobj = {
        // customerId: localStorage.getItem('loginCode'),
        customerId: this.customerId,
        leadId: '' + this.leadRef,
        serviceQuestion: this.questionStdNorm.selectedQuestion,
        serviceAnswer: this.questionStdNorm.selectedOption,
        parentId: this.questionStdNorm.nextQuestionId,
        mainId: this.questionStdNorm.questionId,
        type: this.questionStdNorm.questionType,
        tempParentId: this.tempArray,
        optionIds: this.questionOptionsArray
      }

      console.log("Lead Details", basicobj)
      this.customerServiceDetails(basicobj)


      this.dataServiceProjectDesc = true;
      this.progressBar = "70%";
      this.projectDescription();
    }
    if (this.questionStdNorm.nextQuestionType == "STD") {
      //console.log("in std")
      this.stdButton = true;
      this.catButton = false;
      this.progressBar = "50%";
      this.standardQuestions(this.questionStdNorm.nextQuestionId, this.questionStdNorm.nextQuestionType, count)
    } else if (this.questionStdNorm.nextQuestionType == "CAT") {
      this.stdButton = false;
      this.catButton = true;
      this.progressBar = "20%";
      if (count == undefined) {
      } else {
        this.dataSource.length = count + 1;
      }
      // if(this.dataSource.find(x=>x.QuestionId==this.questionStdNorm.nextQuestionId))
      let existedQuestion = this.dataSource.filter(x => x.QuestionId == this.questionStdNorm.nextQuestionId)

      if (existedQuestion.length == 1) {
        //console.log('length' + existedQuestion)
        let questionType = existedQuestion[0].options
        //console.log("questionType" + JSON.stringify(questionType))
      } else {
        //console.log('length0')
        this.http.get(Global.url + '/getNextQuestionId/' + this.questionStdNorm.nextQuestionId, options)
          .subscribe(data => {
            this.serviceResponseQuestions = data.json()
            if (this.serviceResponseQuestions.status == 200) {
              this.dynamicArr.push({
                "question": this.serviceResponseQuestions.question[0].questions,
                "questionId": this.serviceResponseQuestions.question[0].questionsId,
                "optionType": this.serviceResponseQuestions.question[0].optionType
              })
              //console.log("dynamicArr"+JSON.stringify(this.serviceResponseQuestions.question[0].optionType))
              for (var i = 0; i < this.serviceResponseQuestions.options.length; i++) {
                this.optionsId = this.serviceResponseQuestions.options[i].id
                this.options = this.serviceResponseQuestions.options[i].options
                this.questionId = this.serviceResponseQuestions.options[i].nextQuestionId
                this.questionType = this.serviceResponseQuestions.options[i].nextQuestionType
                this.optionQuestionId = this.serviceResponseQuestions.options[i].questionsId
                this.optionsArray = this.optionsArray.filter(val => val.id != this.optionsId)
                this.optionsArray.push({
                  "id": this.optionsId,
                  "options": this.options,
                  "nextQuestionId": this.questionId,
                  "nextQuestionType": this.questionType,
                  "questionsId": this.optionQuestionId
                })
                this.tempOptionsArray = this.optionsArray.filter(value => value.questionsId == this.optionQuestionId)
                //console.log(this.tempOptionsArray)
              }
              var quesion = this.dynamicArr.filter(value => value.questionId == this.serviceResponseQuestions.question[0].questionsId)
              this.ids = quesion.questionId
              this.dataSource.push({ "Question": this.dynamicArr[this.dataSource.length].question, "QuestionId": this.dynamicArr[this.dataSource.length].questionId, "OptionType": this.dynamicArr[this.dataSource.length].optionType, "options": this.optionsArray[this.dataSource.length], "count": this.dataSource.length, optionsArray: this.tempOptionsArray })
              if (this.questionStdNorm.selectedQuestion == "Is this an emergency" && this.questionStdNorm.selectedOption == "Yes") {
                localStorage.setItem('emergency', JSON.stringify({
                  'question': this.questionStdNorm.selectedQuestion,
                  'answer': this.questionStdNorm.selectedOption
                }))
              } else if (this.questionStdNorm.selectedQuestion == "Is this an emergency" && this.questionStdNorm.selectedOption == "No") {
                localStorage.setItem('emergency', JSON.stringify({
                  'question': this.questionStdNorm.selectedQuestion,
                  'answer': this.questionStdNorm.selectedOption,
                }))
              } else {
                localStorage.setItem('emergency', JSON.stringify({
                  'question': '',
                  'answer': '',
                }))
              }
              this.dataServiceSubCat = true;
              this.dataServiceParent = true;

              let basicobj = {
                // customerId: localStorage.getItem('loginCode'),
                customerId: this.customerId,
                leadId: '' + this.leadRef,
                serviceQuestion: this.questionStdNorm.selectedQuestion,
                serviceAnswer: this.questionStdNorm.selectedOption,
                parentId: this.questionStdNorm.nextQuestionId,
                mainId: this.questionStdNorm.questionId,
                type: this.questionStdNorm.nextQuestionType,
                questionType: this.questionStdNorm.questionType,
                tempParentId: this.tempArray,
                optionIds: this.questionOptionsArray
              }
              this.customerServiceDetails(basicobj)

              return false;
            } else {
              this.dataServiceSubCat = false;
              return false;
            }
          }, error => {
            this.toastr.warning('Unable to Connect to Server')
          })
      }

    }

  }

