  // BASIC DETAILS API


  getMatchingPros() {
    let obj = {
      "zipCode": this.zipCode,
      "categoryName": this.categoryName,
      "subCategoryName": this.subCategoryName,
      "catCode": this.categoryCode,
      "subCatCode": this.subCategoryCode,
      "customerId": this.customerId,
      "leadId": '' + this.leadRef,
      "type": "MULTIPLE_JOB_GENERATION"
    }
    console.log("obj" + JSON.stringify(obj))
    this.http.post(Global.url + '/fetchMatchingPro', obj)
      .subscribe(data => {
        console.log("data" + data)
        var matchingProsData = data.json();
        console.log("matching pro data" + JSON.stringify(matchingProsData))
        if (matchingProsData.status == 200) {
          this.matchingStatus = true;
          this.matchingProData = matchingProsData.response;
          this.addMatchingPro(this.matchingProData)
          this.isMatched({
            isMatched: 'MATCHED',
            leadId: this.leadRef
          })
          // console.log("matchingProData"+JSON.stringify(this.matchingProData))
          //alert("Mails are sending for the Matching Pros")

          // this.router.navigate(['/customer/leadConfirmation', this.activatedRoute.snapshot.params['anyCode']])
        } else if (matchingProsData.status == 404 || matchingProsData.status == 400) {

          this.matchingStatus = false;
          // alert("Someone will reach you shortly from Top Proz")         
          // alert('No Matching Pro')

          // $('html, body').animate({
          //   scrollTop: $('#footer').offset().top
          // }, 1000);
          this.dataFormReg = true;
          // $(window).scrollTop($("#footer").offset().top);
          $('html, body').animate({
            scrollTop: $('#footer').offset().top
          }, 5000);
          this.spinner.hide()
          //this.router.navigate(['/customer/leadConfirmation', this.activatedRoute.snapshot.params['anyCode']])
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }


  isMatched(isMatched) {
    console.log(isMatched)
    return new Promise((resolve, reject) => {
      this.http.post(Global.url + '/updateMatchedProStatusLeadId', isMatched)
        .subscribe(data => {
          console.log(data.json());
          resolve({
            status: 'success'
          })
        })
    })
  }

  addMatchingPro(matchingProData) {
    //console.log("matchingProData"+matchingProData)
    return new Promise((resolve, reject) => {
      this.http.post(Global.url + '/addBatchMatchingPro/', matchingProData)
        .subscribe(data => {
          var matchingData = data.json();
          this.matchingResponse = matchingData.response;
          if (matchingData.status == 200) {


            // alert('proMatching')


            this.dataFormReg = true;
            // $(window).scrollTop($("#footer").offset().top);
            $('html, body').animate({
              scrollTop: $('#footer').offset().top
            }, 5000);
            this.spinner.hide()
            this.matchingResponseStatus = false;
            resolve({
              status: 'success'
            })
            this.toastr.success("Matching Data Added Successfully");
          } else {
            resolve({
              status: 'fail'
            })
            this.toastr.warning("Record adding Failed")
            this.matchingResponseStatus = true;
          }
        }, error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })
    })
  }

