import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../utilities/beyond';

@Component({
  providers: [Beyond],
  selector: 'app-view-proz-profile',
  templateUrl: './view-proz-profile.component.html',
  styleUrls: ['./view-proz-profile.component.css']
})
export class ViewProzProfileComponent implements OnInit {
  prozView: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.prozView = JSON.parse(localStorage.getItem('data'))
    //console.log("prozview"+JSON.stringify(this.prozView))
  }

}
